const chipsSector = document.querySelectorAll('.sector .chip');
const chipsActividades = document.querySelectorAll('.actividad .chip');
const chipsEmpleados = document.querySelectorAll('.empleados .chip');
const circuloAvance = document.getElementById('circuloAvance');
const inputVentas = document.getElementById('ventas');
const icon = document.getElementById('saveAnimatedIcon')


let lastChipSector = null;
let lastChipActividad = null;
let lastChipEmpleados = null;
let sectorElegido = '';
let actividadElegida = '';
let numEmpleados = '';
let porcentajeActual = 0;

function selectOpcionSector(){
  if(lastChipSector!==null) lastChipSector.classList.remove('active');
  this.classList.add('active');
  lastChipSector = this;
  sectorElegido = this.dataset.sector
}

function selectOpcionActividad(){
  if(lastChipActividad!==null) lastChipActividad.classList.remove('active');
  this.classList.add('active');
  lastChipActividad = this;
  actividadElegida = this.dataset.actividad
}

function selectOpcionEmpleados(){
  if(lastChipEmpleados!==null) lastChipEmpleados.classList.remove('active');
  this.classList.add('active');
  lastChipEmpleados = this;
  numEmpleados = this.dataset.empleados
}

function actualizaPorcentajeAvance (porcentaje) {
  porcentajeActual = circuloAvance.dataset.porcentaje;
  circuloAvance.classList.add('p'+porcentaje);
  circuloAvance.dataset.porcentaje = porcentaje;
  circuloAvance.classList.remove('p'+porcentajeActual);
}

// animacion de guardado automatico
function animateSaveIcon () {
  icon.classList.remove('fa-check');
  icon.classList.add('fa-refresh','fa-spin','fa-fw');
  setTimeout(function(){
    icon.classList.remove('fa-refresh','fa-spin','fa-fw');
    icon.classList.add('fa-check','faa-vertical', 'animated');
    setTimeout(function(){ icon.classList.remove('faa-vertical', 'animated'); }, 2000);
  }, 3000);
  setTimeout(() => {

    if (!timeUp) animateSaveIcon();
  }, 8000);
}

let timeUp = false;
function simularTiempo() {

    animateSaveIcon();
    setTimeout(() => timeUp = true, 40000);
  }

setTimeout(() => simularTiempo() , 3000)


$('#ventas').maskMoney();

chipsSector.forEach(chipSector => chipSector.addEventListener('click', selectOpcionSector));

chipsActividades.forEach(chipActividad => chipActividad.addEventListener('click', selectOpcionActividad));

chipsEmpleados.forEach(chipNumEmpleados => chipNumEmpleados.addEventListener('click', selectOpcionEmpleados));
